html, body {
  max-width: 100vw;
  max-height: 100vh;
  overflow: hidden;
}

.webgl {
  outline: none;
  position: fixed;
  top: 0;
  left: 0;
}

header {
  z-index: 5;
  height: 150px;
  position: absolute;
  top: 65%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%);
}

header h1 {
  text-align: center;
  color: #fff;
  z-index: 5;
  width: 550px;
  font-size: 50px;
  font-weight: bold;
  position: absolute;
  top: 65%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loading {
  background-blend-mode: multiply;
  background: linear-gradient(#323232 0%, #3f3f3f 40%, #1c1c1c 150%), linear-gradient(to top, #fff6 0%, #00000040 200%);
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  right: 0;
}

.loading p {
  color: #fff;
  z-index: 10;
  font-size: 75px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

main .started {
  opacity: 0;
  z-index: 11;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 250px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

main .started button {
  cursor: pointer;
  background-color: #fdfdfd;
  border: none;
  border-radius: 50%;
  width: 35px;
  height: 35px;
  transition: all .2s;
}

main .started button:hover {
  opacity: .6;
}

main .started p {
  color: #fff;
  margin-top: 10px;
}

main .player {
  z-index: 5;
  opacity: 0;
  visibility: hidden;
  background-color: #00000075;
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  right: 0;
}

main .player .player-source {
  z-index: 6;
  width: 85vw;
  height: 85vh;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

main .player .player-close {
  cursor: pointer;
  z-index: 7;
  border: none;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  transition: all .2s;
  position: absolute;
  top: 20px;
  right: 20px;
}

main .player .player-close:hover {
  transform: scale(1.05);
}

main .player .player-close:after {
  content: "";
  background-color: #000;
  width: 25px;
  height: 2px;
  position: absolute;
  transform: translate(-50%, -50%)rotate(45deg);
}

main .player .player-close:before {
  content: "";
  background-color: #000;
  width: 25px;
  height: 2px;
  position: absolute;
  transform: translate(-50%, -50%)rotate(-45deg);
}

